table {
    width: 90vw;
    text-align: left;
    color: black;
  }
  table tr:nth-child(even) {
    background: white;
  }
  table tr:nth-child(odd) {
    background: #f8f2f2;
  }
  
  td {
    width: min-content;
  }
  
  .table-data {
    width: 90vw;
    margin: 0 auto;
  }

  ul{
    display: flex !important;
    list-style: none;
    justify-content: space-between !important;
  }